/* eslint-disable jsx-a11y/anchor-has-content */
import React from "react";
import PropTypes from "prop-types";

// components
import cardContactUsHorizontal from "../../Texts/cards/cardContactUsHorizontal.js";
import CardContactUsHorizontal from "./CardContactUsHorizontal";

export default function CardContactUsInfo({ name, location, description }) {
  return (
    <>
      <div className="relative flex flex-col min-w-0 break-words bg-white w-full mb-6 shadow-xl rounded-lg">
        <div className="">
          <div className="text-center mt-12">
            <h3 className="text-4xl font-bold leading-tight mb-2 mb-0">
              {name}
            </h3>
            {location ? (
              <div className="text-sm leading-normal mt-0 mb-2 text-blueGray-500 font-bold uppercase">
                <i className="fas fa-map-marker-alt mr-2 text-lg text-blueGray-500"></i>
                {location}
              </div>
            ) : null}
          </div>

          <div className="mt-10 px-10 py-10 border-t border-blueGray-300 text-center">
            <div className="flex flex-wrap justify-center">
              <div className="w-full lg:w-10/12 px-4">
                <p className="mb-10 text-xl leading-relaxed text-blueGray-800">
                  {description}
                </p>
              </div>
            </div>
          </div>

          <div className="pt-10">
            <CardContactUsHorizontal {...cardContactUsHorizontal} />
          </div>
        </div>
      </div>
    </>
  );
}

CardContactUsInfo.defaultProps = {
  stats: [],
  buttons: [],
  achievements: [],
  showMore: {},
};

CardContactUsInfo.propTypes = {
  image: PropTypes.string,
  name: PropTypes.string,
  location: PropTypes.string,
  // You send a simple string for a one paragraph description
  // or you can send an array of strings, that will generate
  // each string into a new paragraph
  description: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.arrayOf(PropTypes.string),
  ]),
  // properties to pass to the link object
  // if you pass a prop named to, the link will be
  // generated using Link from react-router-dom
  showMore: PropTypes.object,
  stats: PropTypes.arrayOf(
    PropTypes.shape({
      title: PropTypes.string,
      subtitle: PropTypes.string,
    })
  ),
  // array of objects representing the props
  // that you can pass to the Button component
  buttons: PropTypes.arrayOf(PropTypes.object),
  achievements: PropTypes.arrayOf(
    PropTypes.shape({
      icon: PropTypes.string,
      text: PropTypes.string,
    })
  ),
};
