import React from "react";
import ReactDOM from "react-dom";
// import { initializeApp } from "firebase/app";
// import { getAnalytics } from "firebase/analytics";
import { BrowserRouter, Route, Switch, Redirect } from "react-router-dom";

import "@fortawesome/fontawesome-free/css/all.min.css";
import "assets/styles/tailwind.min.css";
import "assets/styles/docs.css";

//Svegile
import SvegileHome from "./Svegile/Views/Home.js";
import Services from "./Svegile/Views/Services.js";
import Discover from "./Svegile/Views/Discover.js";
import Careers from "./Svegile/Views/Careers.js";
import ContactUs from "./Svegile/Views/ContactUs.js";
import AboutUs from "./Svegile/Views/AboutUs.js";
import Resource from "./Svegile/Views/Resource.js";

// TODO: Add SDKs for Firebase products that you want to use
// https://firebase.google.com/docs/web/setup#available-libraries

