ReactDOM.render(
  <BrowserRouter>
    <Switch>
      <Route
        path="/home"
        exact
        render={() => (
          <React.Fragment>
            <div>
              <SvegileHome />
            </div>
          </React.Fragment>
        )}
      />

      <Route
        path="/services"
        exact
        render={() => (
          <React.Fragment>
            <div>
              <Services />
            </div>
          </React.Fragment>
        )}
      />

      <Route
        path="/discover"
        exact
        render={() => (
          <React.Fragment>
            <div>
              <Discover />
            </div>
          </React.Fragment>
        )}
      />

      <Route
        path="/resources"
        exact
        render={() => (
          <React.Fragment>
            <div>
              <Resource />
            </div>
          </React.Fragment>
        )}
      />

      <Route
        path="/careers"
        exact
        render={() => (
          <React.Fragment>
            <div>
              <Careers />
            </div>
          </React.Fragment>
        )}
      />

      <Route
        path="/contactUs"
        exact
        render={() => (
          <React.Fragment>
            <div>
              <ContactUs />
            </div>
          </React.Fragment>
        )}
      />

      <Route
        path="/aboutus"
        exact
        render={() => (
          <React.Fragment>
            <div>
              <AboutUs />
            </div>
          </React.Fragment>
        )}
      />

      <Redirect from="*" to="/home" />
    </Switch>
  </BrowserRouter>,
  document.getElementById("root")
);
