/* eslint-disable jsx-a11y/anchor-has-content */
import React from "react";
import PropTypes from "prop-types";

export default function CardServiceWithList({
  icon,
  title,
  description,
  list,
  id,
}) {
  return (
    <>
      <div
        id={id}
        className="mb-12 mt-12  relative flex flex-col min-w-0 break-words bg-white w-full mb-6 shadow-lg rounded-lg shadow"
      >
        <div className="items-center flex flex-wrap">
          <div className=" w-full lg:w-4/12">
            <div className="p-8 py-5 flex-auto">
              <div
                className={
                  "text-white text-3xl p-3 text-center inline-flex items-center justify-center w-20 h-20 mb-5 shadow-xl hover:shadow-lg rounded-full transition duration-150 ease-in-out" +
                  "bg-gradient-to-r from-lightBlue-600 to-lightBlue-600 bg-lightBlue-400"
                }
              >
                <i className={icon}></i>
              </div>
              <h3 className="text-2xl font-bold uppercase drop-shadow-lg shadow-black">
                {title}
              </h3>
            </div>
          </div>

          <div className="p-8 relative w-full lg:w-8/12 hover:bg-blueGray-100 hover:shadow-lg transition duration-150 ease-in-out">
            <div className="lg:pl-0 px-4 py-5 flex-auto">
              <p className="mt-3 text-lg leading-relaxed text-blueGray-700">
                <span className="mr-2">{description}</span>
              </p>
              <ul className="list-none mt-6">
                {list &&
                  list.map((prop, key) => {
                    return (
                      <li className="py-2" key={key}>
                        <div className="flex items-center">
                          <div>
                            <span
                              className={
                                "text-lightBlue-500 shadow-xl bg-lightBlue-400 hover:bg-lightBlue-200 text-xs inline-block w-8 h-6 px-1 flex rounded-full mr-4"
                              }
                            >
                              <i className={prop.icon}></i>
                            </span>
                          </div>
                          <div>
                            <h4 className="text-blueGray-500">{prop.text}</h4>
                          </div>
                        </div>
                      </li>
                    );
                  })}
              </ul>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
CardServiceWithList.defaultProps = {
  link: {},
  userlink: {},
};
CardServiceWithList.propTypes = {
  image: PropTypes.string,
  title: PropTypes.string,
  description: PropTypes.string,
  // properties to pass to the link object after the description
  // if you pass a prop named to, the link will be
  // generated using Link from react-router-dom
  // NOTE: this will also apply as a wrapper for the title
  link: PropTypes.object,
  // this is an image for the user
  user: PropTypes.string,
  username: PropTypes.string,
  // properties to pass to the link object that
  // wrapps the user, date and username
  // if you pass a prop named to, the link will be
  // generated using Link from react-router-dom
  userlink: PropTypes.object,
  date: PropTypes.string,
};
