import React from "react";
import PropTypes from "prop-types";

// components
import Button from "../Elements/Button.js";

export default function OpenPositionCard({
  icon,
  color,
  title,
  list,
  button,
  location,
  jobType,
}) {
  let textColor = {
    light: "text-blueGray-400",
    dark: "text-blueGray-800",
    blueGray: "text-blueGray-500",
    red: "text-red-500",
    orange: "text-orange-500",
    amber: "text-amber-500",
    emerald: "text-emerald-500",
    teal: "text-teal-500",
    lightBlue: "text-lightBlue-500",
    indigo: "text-indigo-500",
    purple: "text-purple-500",
    pink: "text-pink-500",
  };
  return (
    <>
      <div className="justify-center flex flex-wrap -mx-4">
        <div className="px-4 relative w-full lg:w-10/12">
          <div className="shadow-lg rounded-lg bg-white relative flex flex-col min-w-0 break-words bg-white w-full mb-6">
            <div className="p-12 flex-auto">
              <div className="items-center flex flex-wrap -mx-4">
                <div className="px-4 relative w-full lg:w-9/12">
                  <h6
                    className={
                      textColor[color] + "text-xl font-bold mt-0 mb-0 uppercase"
                    }
                  >
                    {title}
                  </h6>
                  <ul className="my-8 list-none">
                    {list.map((prop, key) => (
                      <li key={key} className="py-2">
                        <h6 className="text-lg">{prop.role}</h6>
                      </li>
                    ))}
                  </ul>
                  <div className="flex">
                    <i className="fas fa-map-marker-alt text-lg ml-2 text-lightBlue-500"></i>
                    <span className="h6 text-lg ml-2">{location}</span>
                    <i className="fas fa-user-tie ml-8 text-lg text-lightBlue-500"></i>
                    <span className="h6 text-lg ml-2">{jobType}</span>
                  </div>
                </div>
                <div className="px-10 relative w-full justify-center lg:w-3/12">
                  <Button
                    {...button}
                    onClick={(e) => {
                      let applyNow = document.getElementById("ApplyNow");
                      e.preventDefault(); // Stop Page Reloading
                      applyNow &&
                        setTimeout(function () {
                          applyNow.scrollIntoView({
                            behavior: "smooth",
                            block: "start",
                          });
                        }, 100);
                    }}
                  ></Button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
OpenPositionCard.defaultProps = {
  color: "blueGray",
  button: {},
};

OpenPositionCard.propTypes = {
  // icon name to be added on the <i></i> tag, for example "fa fa-heart"
  icon: PropTypes.string,
  color: PropTypes.oneOf([
    "light",
    "dark",
    "blueGray",
    "red",
    "orange",
    "amber",
    "emerald",
    "teal",
    "lightBlue",
    "indigo",
    "purple",
    "pink",
  ]),
  list: PropTypes.array,
  // if the background should have a gradient
  title: PropTypes.string,
  description: PropTypes.string,
  // props to pass to the Button component
  button: PropTypes.object,
};
