import React from "react";
import PropTypes from "prop-types";

// components
import AboutUsHeader from "../Components/Headers/AboutUsHeader.js";
import OpenPositionCard from "../Components/Cards/OpenPositionCard.js";

export default function OpenPositions({ headingBadge, image, cards }) {
  return (
    <>
      <div className="relative py-10">
        <div className="mx-auto px-4">
          <AboutUsHeader {...headingBadge} />
          <div className="items-center flex flex-wrap -mx-4">
            <div className="px-4 relative w-full">
              {cards.map((prop, key) => (
                <OpenPositionCard key={key} {...prop} />
              ))}
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

OpenPositions.defaultProps = {
  cards: [],
  heading3badge: null,
};
OpenPositions.propTypes = {
  // prop to pass to the Heading3Badge component
  heading3badge: PropTypes.object,
  image: PropTypes.string,
  // array of props to pass to the CardInfoIconLeft component
  cards: PropTypes.arrayOf(PropTypes.object),
};
