import React from "react";
import PropTypes from "prop-types";

// components
import CardInfoFullColor from "../Components/Cards/CardInfoFullColor.js";
import AboutUsHeader from "../Components/Headers/AboutUsHeader.js";

export default function ServiceIntro({ headingBadge, infoAreas }) {
  return (
    <>
      <div className="py-8">
        <div className="px-10">
          <AboutUsHeader {...headingBadge} />
        </div>
        <div className="justify-center flex flex-wrap">
          <div className="justify-center px-1 relative w-full lg:w-2/3">
            <div className="items-center flex flex-wrap">
              {infoAreas.map((prop, key) => {
                return (
                  <div
                    className="text-left px-4 relative w-full lg:w-1/2"
                    key={key}
                  >
                    <CardInfoFullColor {...prop} />
                  </div>
                );
              })}
            </div>
          </div>
        </div>

        <div className="flex justify-center pt-12">
          <a
            color="lightBlue"
            size="lg"
            type="button"
            href="#services"
            className=" text-lightBlue-500 bg-transparent border border-solid border-lightBlue-500 hover:bg-lightBlue-500 hover:text-white active:bg-lightBlue-600 font-bold uppercase text-sm px-6 py-4 rounded-full outline-none focus:outline-none mr-1 mb-12 ease-linear transition-all duration-150"
          >
            All Services
          </a>
        </div>
      </div>
    </>
  );
}

ServiceIntro.defaultProps = {
  infoAreas: [],
  headingBadge: null,
};
ServiceIntro.propTypes = {
  // prop to pass to the headingBadge component
  headingBadge: PropTypes.object,
  // an array of props that can be passed to the
  // InfoAreaIconTitleLinkColor component
  infoAreas: PropTypes.arrayOf(PropTypes.object),
};
