import React from "react";
import PropTypes from "prop-types";

// sections
import Vision from "./VisionCards.js";

// components
import AboutUsHeader from "../Components/Headers/AboutUsHeader.js";
import CardVision from "../Components/Cards/CardVision.js";
import Globe from "../Components/SoftBox/globe.js";

//texts
import visionDetails from "../Texts/Visions.js";

const vision = {
  color: "white",
  gradient: true,
  title: "Our Vision",
  description:
    "When we work together, we create a stronger team than the sum of its parts. We overcome differences to achieve extraordinary results because we have a common goal  to make the future together.",
};

export default function HomeVision({ headingBadge }) {
  return (
    <>
      <div className="py-12">
      <AboutUsHeader {...headingBadge}/>

        <div className="mx-auto px-4">
          <div className="flex flex-wrap">
            <div className="mx-auto flex justify-center flex-col text-white relative lg:w-5/12 w-full md:w-5/12">
              <Globe
                display={{ xs: "none", md: "block" }}
                position="absolute"
                top="0%"
                left={1}
                mt={{ xs: -12, lg: 1 }}
                mr={{ xs: 0, lg: 10 }}
                canvasStyle={{ marginTop: "3rem" }}
              />
            </div>
            <div className="mx-auto px-2 relative w-full lg:w-7/12 w-full md:w-7/12">
              <CardVision {...vision} />
              <div className="mx-auto px-2 relative w-full w-full">
                <Vision {...visionDetails} />
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

HomeVision.defaultProps = {
  headingBadge: null,
  mediaplayer: {},
};
HomeVision.propTypes = {
  headingBadge: PropTypes.object,
};
