import React from "react";
import PropTypes from "prop-types";

// components
import CardVision from "../Components/Cards/CardVision.js";

export default function Hero4({
  rightCardInfoIconProps,
  rightCardBlogImageProps,
  rightCardContactUsTitleProps,
  rightCardInfoImageProps,
}) {
  return (
    <>
      <div className="py-2">
        <div className="items-center flex flex-wrap -mx-4">
          <div className="mr-auto px-2 relative w-full">
            <div className="flex flex-wrap -mx-4">
              <div className="px-2 relative w-full md:w-6/12">
                <CardVision {...rightCardInfoIconProps} />
              </div>
              <div className="px-2 relative w-full md:w-6/12">
                <CardVision {...rightCardBlogImageProps} />
              </div>
            </div>
            <div className="flex flex-wrap -mx-4">
              <div className="px-2 relative w-full md:w-6/12">
                <CardVision {...rightCardContactUsTitleProps} />
              </div>
              <div className="px-2 relative w-full md:w-6/12">
                <CardVision {...rightCardInfoImageProps} />
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
Hero4.defaultProps = {
  headingBadge: null,
  leftCardProps: {},
  rightCardInfoIconProps: {},
  rightCardBlogImageProps: {},
  rightCardContactUsTitleProps: {},
  rightCardInfoImageProps: {},
};
Hero4.propTypes = {
  // prop to pass to the HeadingBadge component
  headingBadge: PropTypes.object,
  // Please check CardInfoImage props for more info
  leftCardProps: PropTypes.object,
  // Please check CardInfoFullColor props for more info
  rightCardInfoIconProps: PropTypes.object,
  // Please check CardInfoFullColor props for more info
  rightCardBlogImageProps: PropTypes.object,
  // Please check CardInfoFullColor props for more info
  rightCardContactUsTitleProps: PropTypes.object,
  // Please check CardInfoFullColor props for more info
  rightCardInfoImageProps: PropTypes.object,
};
