import React from "react";
import { useLocation } from "react-router-dom";
import TagManager from 'react-gtm-module';

//Header
import SvegileNavbar from "../Components/Navbar/SvegileNavbar.js";
import svegileNavbar from "../Texts/navbar/svegileNavbar.js";

// sections
import CoreValues from "../Sections/CoreValues.js";
import ContactUsFooter from "../Sections/ContactFooter.js";
import FooterLarge from "../Sections/FooterLarge.js";

// components
import AboutUsHeader from "../Components/Headers/AboutUsHeader.js";

// texts as props
import aboutUsHeader from "../Texts/headers/aboutUsHeader.js";
import aboutUsInfo from "../Texts/aboutUsInfo.js";
import aboutUsContact from "../Sections/AboutUsContact.js";
import footerLarge from "../Texts/footerLarge.js";

const tagManagerArgs = {
  gtmId: 'G-P70K0V7E32'
}

TagManager.initialize(tagManagerArgs)

export default function AboutUs() {
  const location = useLocation();
  React.useEffect(() => {
    window.scrollTo(0, 0);
  }, [location]);
  return (
    <>
      <SvegileNavbar {...svegileNavbar} />
      <main>
        <div className="pt-17">
          <AboutUsHeader {...aboutUsHeader} />
        </div>
        <div className="mx-auto px-4">
          <CoreValues {...aboutUsInfo} />
        </div>
        <ContactUsFooter {...aboutUsContact} />
      </main>
      <div className="-mt-20">
        <FooterLarge {...footerLarge} />
      </div>
    </>
  );
}
