import React from "react";
import { useLocation } from "react-router-dom";

//Header
import SvegileNavbar from "../Components/Navbar/SvegileNavbar.js";
import svegileNavbar from "../Texts/navbar/svegileNavbar.js";

//Sections
import ServiceDetails from "../Sections/ServiceDetails.js";
import ServiceContact from "../Sections/ServiceContact.js";
import FooterLarge from "../Sections/FooterLarge.js";

// components
import ServiceHeader from "../Components/Headers/ServiceHeader.js";

// texts for components as props
import serviceHeader from "../Texts/headers/serviceHeader.js";
import serviceContact from "../Texts/serviceContact.js";
import infoAreas from "../Texts/serviceDetails.js";
import footerLarge from "../Texts/footerLarge.js";

import TagManager from 'react-gtm-module';

const tagManagerArgs = {
  gtmId: 'G-P70K0V7E32'
}
TagManager.initialize(tagManagerArgs)

/*
  const heading2 = {
    description:
      "We believe in changing how consulting firms, freelancers, and outsourcing companies do business by making it easier to manage their businesses and stick to a budget.",
    alignment: "right",
  };
*/

export default function Services() {
  const location = useLocation();
  React.useEffect(() => {
    window.scrollTo(0, 0);
  }, [location]);
  return (
    <>
      <div className="bg-blueGray-100">
        <SvegileNavbar {...svegileNavbar} />
        <main>
          <div className="mx-auto ">
            <div className="pt-17"></div>
            <ServiceHeader {...serviceHeader} />
            <div className="py-12">
              <ServiceDetails {...infoAreas.infoAreas} />
            </div>
            <ServiceContact {...serviceContact} />
          </div>
        </main>
        <div className="-mt-20">
          <FooterLarge {...footerLarge} />
        </div>
      </div>
    </>
  );
}
