import React from "react";
import PropTypes from "prop-types";

// components
import HeadingBadge from "../Components/Heading/HeadingBadge.js";
import AboutUsHeader from "../Components/Headers/AboutUsHeader.js";
import CardProjectImageFullBg from "../Components/Cards/CardProjectImageFullBg.js";

import CoreValuesCard from "../Components/Cards/CoreValuesCard.js";

export default function CoreValues({
  headingBadge,
  cards,
  AboutUsQuote1,
  values,
}) {
  return (
    <>
      <section className="relative py-1">
        <div className="container mx-auto px-4">
          <div className="mb-12">
            {headingBadge && <HeadingBadge {...headingBadge} />}
          </div>
          <div className="flex flex-wrap px-4">
            {cards.map((prop, key) => (
              <div key={key} className="px-4 relative w-full md:w-1/4">
                <CardProjectImageFullBg {...prop} />
              </div>
            ))}
          </div>
          <div className="pt-12">
            <AboutUsHeader {...AboutUsQuote1} />
          </div>
          <div className="flex flex-wrap -mx-4 pb-12">
            {values.map((prop, key) => (
              <div key={key} className="px-4 relative w-full md:w-4/12">
                <CoreValuesCard {...prop} />
              </div>
            ))}
          </div>
        </div>
      </section>
    </>
  );
}

CoreValues.defaultProps = {
  cards: [],
  button: {},
  headingBadge: null,
};
CoreValues.propTypes = {
  // prop to pass to the HeadingBadge component
  headingBadge: PropTypes.object,
  // prop to pass to the Button component
  button: PropTypes.object,
  // array of props to pass to the CardProductPlain component
  cards: PropTypes.arrayOf(PropTypes.object),
};
