import React from "react";
import { useLocation } from "react-router-dom";

//Header
import SvegileNavbar from "../Components/Navbar/SvegileNavbar.js";
import svegileNavbar from "../Texts/navbar/svegileNavbar.js";

// components
import ContactUsInfo from "../Components/Cards/CardContactUsInfo.js";
import FooterLarge from "../Sections/FooterLarge.js";
import ContactUsHeader from "../Components/Headers/ContactUsHeader.js";

// texts as props
import contactUsInfo from "../Texts/contactUsInfo.js";
import footerLarge from "../Texts/footerLarge.js";
import contactUsHeader from "../Texts/headers/contactUsHeader.js";

import TagManager from 'react-gtm-module';

const tagManagerArgs = {
  gtmId: 'G-P70K0V7E32'
}
TagManager.initialize(tagManagerArgs)

export default function ContactUs() {
  const location = useLocation();
  React.useEffect(() => {
    window.scrollTo(0, 0);
  }, [location]);
  return (
    <>
      <SvegileNavbar {...svegileNavbar} />
      <main className="pb-20">
        <div className="pt-17">
          <ContactUsHeader {...contactUsHeader} />
        </div>
        <section className="relative pt-32 bg-blueGray-100">
          <div className="mx-auto lg:px-4 -mt-64">
            <ContactUsInfo {...contactUsInfo} />
          </div>
        </section>
      </main>
      <div className="-mt-20">
        <FooterLarge {...footerLarge} />
      </div>
    </>
  );
}
