import React from "react";
import PropTypes from "prop-types";
import Typed from "typed.js";
import { useEffect, useRef } from "react";

// components
import Skew from "../Components/Skew.js";

export default function HomeVideo({
  video,
  title,
  subtitle,
  description,
  skew,
  typeStrings,
}) {
  const el = useRef(null);

  useEffect(() => {
    const typed = new Typed(el.current, {
      strings: typeStrings, // Strings to display
      startDelay: 300,
      typeSpeed: 200,
      backSpeed: 100,
      backDelay: 100,
      loop: true,
      smartBackspace: false,
    });
    // Destroy typing
    return () => {
      typed.destroy();
    };
  }, [typeStrings]);

  return (
    <>
      <div className="p-0 flex items-center overflow-hidden relative bg-black min-h-screen-75">
        <div className="absolute w-full bg-indigo-500 h-full z-1 opacity-30"></div>
        <video
          playsInline="playsInLine"
          autoPlay="autoplay"
          muted="muted"
          loop="loop"
          className="absolute min-w-full max-w-none min-h-6 z-0 transform -translate-x-1/2 -translate-y-1/2 top-7/20 left-1/2"
        >
          <source src={video} type="video/mp4" />
        </video>
        <div className="mx-auto">
          <div className="relative text-left flex flex-wrap -mx-3 z-1">
            <div className="mx-auto px-4 relative w-full lg:w-8/8">
              <h1 className="text-6xl text-left font-bold leading-tight text-white">
                {title}
              </h1>
              <h1 className="mt-2 text-3xl text-white opacity-75">
                {description}
                <br/>
              <span className="mt-2 text-3xl text-white opacity-75 mb-8">
                {subtitle}
                <span
                  className="text-4xl text-bold text-lightBlue-500"
                  ref={el}
                ></span>
              </span>
              </h1>
            </div>
          </div>
        </div>
        <div className="w-full absolute text-white bottom-0 z-1">
          <Skew {...skew} />
        </div>
      </div>
    </>
  );
}

HomeVideo.defaultProps = {
  skew: {},
};

HomeVideo.propTypes = {
  video: PropTypes.string.isRequired,
  title: PropTypes.string.isRequired,
  subtitle: PropTypes.string.isRequired,
  buttonIcon: PropTypes.string,
  buttonColor: PropTypes.oneOf([
    "light",
    "dark",
    "blueGray",
    "red",
    "orange",
    "amber",
    "emerald",
    "teal",
    "lightBlue",
    "indigo",
    "purple",
    "pink",
  ]),
  // props to pass to the Skew component
  skew: PropTypes.object,
  typeStrings: PropTypes.arrayOf(String),
};
