import React from "react";
import PropTypes from "prop-types";

export default function CardProjectImageFullBg({ image, title, alignment }) {
  const alignmentsText = {
    left: "text-left",
    center: "text-center",
    right: "text-right",
  };
  return (
    <>
      <div
        className="relative flex flex-col min-w-0 break-words bg-white w-full h-48 mb-6 shadow-lg rounded-lg bg-50 bg-cover p-10"
        style={{ backgroundImage: "url('" + image + "')" }}
      >
        <div className="absolute w-full h-48 top-0 left-0 bg-black opacity-60 z-0 rounded-lg"></div>
        <div
          className={
            alignmentsText[alignment] +
            " flex-auto z-1 absolute inset-x-0 bottom-0"
          }
        >
          <h3 className="text-2xl font-bold mt-0 mb-2 text-white leading-tight">
            {title}
          </h3>
        </div>
      </div>
    </>
  );
}
CardProjectImageFullBg.defaultProps = {
  button: {},
  color: "lightBlue",
  alignment: "center",
};
CardProjectImageFullBg.propTypes = {
  image: PropTypes.string,
  title: PropTypes.string,
  description: PropTypes.string,
  // props to pass to the Button component
  button: PropTypes.object,
  icon: PropTypes.string,
  alignment: PropTypes.oneOf(["left", "center", "right"]),
  color: PropTypes.oneOf([
    "blueGray",
    "red",
    "orange",
    "amber",
    "emerald",
    "teal",
    "lightBlue",
    "indigo",
    "purple",
    "pink",
  ]),
};
