/*eslint-disable*/
import React from "react";
import { useLocation } from "react-router-dom";

//Header
import SvegileNavbar from "../Components/Navbar/SvegileNavbar.js";
import CardCareerApply from "../Components/Cards/CardCareerApply.js";
import CareerHeader from "../Components/Headers/CareerHeader.js";

// components
import CareerInfo from "../Sections/CareerInfo.js";
import OpenPositions from "../Sections/OpenPositions.js";
import FooterLarge from "../Sections/FooterLarge.js";

// texts for components as props
import careerHeader from "../Texts/headers/careerHeader.js";
import careerInfo from "../Texts/careerInfo.js";
import openPositions from "../Texts/openPositions.js";
import cardCareerApply from "../Texts/cards/cardCareerApply.js";
import footerLarge from "../Texts/footerLarge.js";
import svegileNavbar from "../Texts/navbar/svegileNavbar.js";

import TagManager from 'react-gtm-module';

const tagManagerArgs = {
  gtmId: 'G-P70K0V7E32'
}

TagManager.initialize(tagManagerArgs)

export default function Careers() {
  const location = useLocation();
  React.useEffect(() => {
    window.scrollTo(0, 0);
  }, [location]);
  return (
    <>
      <SvegileNavbar {...svegileNavbar} />
      <main>
        <div className="pt-17">
          <CareerHeader {...careerHeader} />
        </div>
        <CareerInfo {...careerInfo} />
        <div className="mx-auto px-4 bg-lightBlue">
          <OpenPositions {...openPositions} />
        </div>
        <div className="mx-auto px-4 pb-12" id="ApplyNow">
          <CardCareerApply {...cardCareerApply} />
        </div>
      </main>
      <div className="-mt-20">
        <FooterLarge {...footerLarge} />
      </div>
    </>
  );
}
