import React from "react";
import PropTypes from "prop-types";

export default function HeadingBadge({
  title,
  description,
  sub_line1,
  sub_line2,
  alignment,
  whiteContent,
}) {
  const alignmentsRow = {
    left: "justify-start",
    center: "justify-center",
    right: "justify-end",
  };
  const alignmentsText = {
    left: "text-left",
    center: "text-center",
    right: "text-right",
  };
  return (
    <>
      <div className={"flex flex-wrap -mx-3 " + alignmentsRow[alignment]}>
        <div className={"px-4 relative w-full " + alignmentsText[alignment]}>
          {title && (
            <h2
              className={
                "text-4xl font-bold mt-3 mb-1 " +
                (whiteContent ? "text-white" : "text-blueGray-700")
              }
            >
              {title}
            </h2>
          )}
          {description && (
            <p
              className={
                "mt-2 mb-4 text-lg pt-4 " +
                (whiteContent ? "text-white" : "text-blueGray-700")
              }
            >
              {description}
              <br />
              {sub_line1}
              <br />
              {sub_line2}
            </p>
          )}
        </div>
      </div>
    </>
  );
}

HeadingBadge.defaultProps = {
  color: "blueGray",
  alignment: "center",
  whiteContent: false,
};

HeadingBadge.propTypes = {
  whiteContent: PropTypes.bool,
  title: PropTypes.string,
  description: PropTypes.string,
  alignment: PropTypes.oneOf(["left", "center", "right"]),
};
