import React from "react";
import { useLocation } from "react-router-dom";

//Header
import SvegileNavbar from "../Components/Navbar/SvegileNavbar.js";
import svegileNavbar from "../Texts/navbar/svegileNavbar.js";

// sections
import Engagement from "../Sections/Engagement.js";
import ContactFooter from "../Sections/ContactFooter.js";
import Technology from "../Sections/Technology.js";
import FooterLarge from "../Sections/FooterLarge.js";

// components
import AboutUsHeader from "../Components/Headers/AboutUsHeader.js";
import CardBlogFullBg from "../Components/Cards/CardBlogFullBg.js";
import HeadingBadge from "../Components/Heading/HeadingBadge.js";

// texts for sections as props
import engagement from "../Texts/engagement.js";
import technology_frontend from "../Texts/technologies/technology_frontend.js";
import technology_backend from "../Texts/technologies/technology_backend.js";
import technology_mobile from "../Texts/technologies/technology_mobile.js";
import technology_cloud from "../Texts/technologies/technology_cloud.js";
import discoverContact from "../Texts/discoverContact.js";
import footerLarge from "../Texts/footerLarge.js";
import headerPostDark from "../Texts/headers/headerPostDark.js";

import TagManager from 'react-gtm-module';

const tagManagerArgs = {
  gtmId: 'G-P70K0V7E32'
}
TagManager.initialize(tagManagerArgs)

export default function Discover() {
  const location = useLocation();
  React.useEffect(() => {
    window.scrollTo(0, 0);
  }, [location]);

  const discoverImage = {
    id: "Engagement",
    image: require("assets/img/svegile/softwareCycle.png").default,
    size: "lg",
    rounded: false,
  };
  const technologyHeader = {
    title: "Technologies",
    alignment: "center",
  };

  return (
    <>
      <SvegileNavbar {...svegileNavbar} />
      <main>
        <div className="pt-17">
        <AboutUsHeader {...headerPostDark} />
        </div>
        <div className="mx-auto px-4 pt-10 pb-10">
          <CardBlogFullBg {...discoverImage} />
          </div>
          <div className="mx-auto px-4 pt-10">
          <Engagement {...engagement} />
        </div>
        <div className="mx-auto px-4 pt-6">
          <HeadingBadge {...technologyHeader} />
        </div>
        <div className="mx-auto px-4 pb-6" id="Technology">
          <Technology {...technology_frontend} />
          <Technology {...technology_backend} />
          <Technology {...technology_mobile} />
          <Technology {...technology_cloud} />
        </div>
        <ContactFooter {...discoverContact} />
      </main>
      <div className="-mt-20">
        <FooterLarge {...footerLarge} />
      </div>
    </>
  );
}
