import React from "react";
import PropTypes from "prop-types";

// components
import InfoAreaIconTitleListColor from "../Components/InfoAreas/InfoAreaIconTitleListColor.js";
import CareerCard from "../Components/Cards/CareerCard.js";

export default function CareerInfo({ infoArea, cards }) {
  return (
    <>
      <div className="relative text-center py-10">
        <div className="container mx-auto px-4">
          <div className="items-center flex flex-wrap -mx-4">
            <div className="mr-auto text-left px-8 relative w-full lg:w-4/12 w-full md:w-8/12">
              <InfoAreaIconTitleListColor {...infoArea} />
            </div>
            <div className="md:pl-0 px-4 relative w-full lg:w-7/12 w-full md:w-full">
              <div className="flex flex-wrap">
                {cards.map((prop, key) => (
                  <div
                    key={key}
                    className={
                      "relative w-full md:w-4/12 -ml-8 opacity-90 mb-8" +
                      (key % 3 === 0 ? " md:mt-2" : "") +
                      (key % 3 === 1 ? " md:-mt-4" : "") +
                      (key % 3 === 2 ? " md:mt-4" : "")
                    }
                  >
                    <CareerCard {...prop} />
                  </div>
                ))}
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

CareerInfo.defaultProps = {
  infoArea: {},
  cards: [],
  heading2: null,
};
CareerInfo.propTypes = {
  heading2: PropTypes.object,
  infoArea: PropTypes.object,
  cards: PropTypes.arrayOf(PropTypes.object),
};
