import React from "react";
import PropTypes from "prop-types";

// components
import InfoAreaUser from "../Components/InfoAreas/InfoAreaUser.js";

export default function Technology({ heading3, infoAreas }) {
  const alignmentsRow = {
    left: "justify-start",
    center: "justify-center",
    right: "justify-end",
  };
  const alignmentsText = {
    left: "text-left",
    center: "text-center",
    right: "text-right",
  };

  return (
    <>
      <div className="container mx-auto px-4">
        <div
          className={
            "flex flex-wrap -mx-3 pt-4 pb-4 px-4" +
            alignmentsRow[heading3.alignment]
          }
        >
          <div
            className={
              "px-4 relative w-full " + alignmentsText[heading3.alignment]
            }
          >
            {heading3.title && (
              <h2 className={"text-xl font-bold mt-3 mb-1 text-blueGray-700"}>
                {heading3.title}
              </h2>
            )}
          </div>
        </div>
        <div className="flex flex-wrap">
          {infoAreas.map((prop, key) => {
            return (
              <div
                className="w-full md:w-3/12 lg:w-2/12 lg:mb-0 mb-12 px-4"
                key={key}
              >
                <InfoAreaUser {...prop} />
              </div>
            );
          })}
        </div>
      </div>
    </>
  );
}

Technology.defaultProps = {
  heading3: null,
  infoAreas: [],
};
Technology.propTypes = {
  // prop to pass to the Heading3 component
  heading3: PropTypes.object,
  // it is represented by the props
  // that can be passed to the InfoAreaUser,
  // so please check that one out
  infoAreas: PropTypes.arrayOf(PropTypes.object),
};
