import React from "react";
import { useLocation } from "react-router-dom";

//Header
import SvegileNavbar from "../Components/Navbar/SvegileNavbar.js";
import svegileNavbar from "../Texts/navbar/svegileNavbar.js";

// sections
import HomeVision from "../Sections/HomeVision.js";
import ServiceIntro from "../Sections/ServiceIntro.js";
import ContactFooter from "../Sections/ContactFooter.js";
import Focus from "../Sections/Focus.js";
import FooterLarge from "../Sections/FooterLarge.js";
import HomeVideo from "../Sections/HomeVideo.js";

// texts for sections as props
import homeVideo from "../Texts/homeVideo.js";
import visionHeader from "../Texts/headers/visionHeader.js";
import serviceIntro from "../Texts/servicesIntro.js";
import focus from "../Texts/focus.js";
import contactFooter from "../Texts/contactUsFooter.js";
import footerLarge from "../Texts/footerLarge.js";

import TagManager from 'react-gtm-module';

const tagManagerArgs = {
  gtmId: 'G-P70K0V7E32'
}
TagManager.initialize(tagManagerArgs)

export default function Landing2() {
  const location = useLocation();
  React.useEffect(() => {
    window.scrollTo(0, 0);
  }, [location]);
  return (
    <div>
      <SvegileNavbar {...svegileNavbar} />
      <main>
        <div className="pt-17"></div>
        <HomeVideo {...homeVideo} />
        <div className="mx-auto lg:px-1">
          <ServiceIntro {...serviceIntro} />
          <Focus {...focus} />
        </div>
        <div className="mx-auto lg:px-1">
          <HomeVision {...visionHeader} />
        </div>
        <ContactFooter {...contactFooter} />
      </main>
      <div className="-mt-20">
        <FooterLarge {...footerLarge} />
      </div>
    </div>
  );
}
