/*eslint-disable*/
import React from "react";
import PropTypes from "prop-types";
import { Link } from "react-router-dom";
import classnames from "classnames";

// components
import DropdownNavbar from "./DropdownNavbar.js";
import DropdownHoverCSSNavbar from "./DropdownHoverCSSNavbar.js";

export default function SvegileNavbar({
  leftLinks,
  rightLinks,
  logoImage,
  logoText,
  logoLink,
  color,
  type,
}) {
  const [collapseOpen, setCollapseOpen] = React.useState(false);
  const [collapseStyle, setCollapseStyle] = React.useState(undefined);
  const [animation, setAnimation] = React.useState(false);
  const collapseRef = React.useRef(null);
  const startAnimation = () => {
    if (!animation) {
      setAnimation(true);
      if (collapseOpen) {
        setCollapseStyle(0);
        setTimeout(function () {
          setAnimation(false);
          setCollapseOpen(false);
        }, 310);
      } else {
        setCollapseOpen(true);
        setTimeout(function () {
          setCollapseStyle(collapseRef.current.scrollHeight);
        }, 10);
        setTimeout(function () {
          setAnimation(false);
        }, 310);
      }
    }
  };
  const navBg = {
    dark: "bg-blueGray-800 shadow-md",
    light: "bg-blueGray-200 shadow-md",
    transparent: "lg:bg-transparent bg-white",
    white: "bg-white shadow-md",
    black: "bg-black shadow-md",
    blueGray: "bg-blueGray-500 shadow-md",
    red: "bg-red-500 shadow-md",
    orange: "bg-orange-500 shadow-md",
    amber: "bg-amber-500 shadow-md",
    emerald: "bg-emerald-500 shadow-md",
    teal: "bg-teal-500 shadow-md",
    lightBlue: "bg-lightBlue-500 shadow-md",
    indigo: "bg-indigo-500 shadow-md",
    purple: "bg-purple-500 shadow-md",
    pink: "bg-pink-500 shadow-md",
  };
  const linkColors = {
    light: "text-blueGray-700",
    dark: "text-white",
    transparent: "lg:text-white text-blueGray-500",
    white: "text-blueGray-700",
    black: "text-white",
    blueGray: "text-white",
    red: "text-white",
    orange: "text-white",
    amber: "text-white",
    emerald: "text-white",
    teal: "text-white",
    lightBlue: "text-white",
    indigo: "text-white",
    purple: "text-white",
    pink: "text-white",
  };
  return (
    <>
      <nav
        className={
          type +
          " w-full z-50 flex flex-wrap items-center justify-between px-2 py-3 mb-3 " +
          navBg[color]
        }
      >
        <div className="flex container mx-auto flex-wrap items-center justify-between px-0 lg:px-4">
          <Link
            {...logoLink}
            className={
              "text-lg font-bold leading-relaxed inline-flex items-center mr-4 py-2 whitespace-nowrap uppercase " +
              linkColors[color]
            }
          >
            <img
              src={logoImage}
              className="mr-2 rounded-lg"
              style={{ width: "30px" }}
            />
            <span>{logoText}</span>
          </Link>

          <button
            className="ml-auto cursor-pointer text-xl leading-none px-3 py-1 border border-solid border-blueGray-400 rounded bg-transparent block outline-none focus:outline-none text-blueGray-300 lg:hidden"
            type="button"
            onClick={startAnimation}
          >
            <i className="fas fa-bars"></i>
          </button>

          <div
            className={classnames(
              "items-center w-full lg:flex lg:w-auto flex-grow duration-300 transition-all ease-in-out",
              {
                "block h-0": collapseOpen,
                hidden: !collapseOpen,
                "overflow-hidden": animation,
              }
            )}
            style={{
              height: collapseStyle,
            }}
            ref={collapseRef}
          >
            <ul className="lg:items-center flex-wrap flex flex-col list-none pl-0 mb-0 lg:flex-row">
              {leftLinks.map((prop, key) => {
                if (prop.to) {
                  <li key={key}>
                    <Link
                      {...prop}
                      className={
                        "hover:opacity-75 px-3 py-4 lg:py-2 flex items-center text-xs uppercase font-bold transition-all duration-150 ease-in-out " +
                        linkColors[color]
                      }
                    />
                  </li>;
                } else if (prop.dropdown) {
                  return (
                    <li className="relative" key={key}>
                      <DropdownNavbar {...prop} navColor={color} />
                    </li>
                  );
                } else {
                  return (
                    <li key={key}>
                      <a
                        {...prop}
                        className={
                          "hover:opacity-75 px-3 py-4 lg:py-2 flex items-center text-xs uppercase font-bold transition-all duration-150 ease-in-out " +
                          linkColors[color]
                        }
                      />
                    </li>
                  );
                }
              })}
            </ul>

            <ul className="lg:items-center lg:ml-auto flex-wrap flex flex-col list-none pl-0 mb-0 lg:flex-row">
              {rightLinks.map((prop, key) => {
                if (prop.to) {
                  <li key={key}>
                    <Link
                      {...prop}
                      className={
                        "hover:opacity-75 hover:text-lg px-3 py-4 lg:py-2 flex items-center text-xs uppercase font-bold transition-all duration-150 ease-in-out " +
                        linkColors[color]
                      }
                    />
                  </li>;
                } else if (prop.dropdown) {
                  return (
                    <li key={key}>
                      <DropdownHoverCSSNavbar {...prop} _href = {prop.href} navColor={color} />
                    </li>
                  );
                } else {
                  return (
                    <li key={key}>
                      <a
                        {...prop}
                        className={
                          "hover:opacity-75 px-3 py-4 lg:py-2 flex items-center text-xs uppercase font-bold transition-all duration-150 ease-in-out " +
                          linkColors[color]
                        }
                      />
                    </li>
                  );
                }
              })}
              <li>
                <a
                  className="text-lightBlue-500 bg-transparent border border-solid border-lightBlue-500 hover:bg-lightBlue-500 hover:text-white active:bg-lightBlue-600 font-bold uppercase text-sm px-6 py-3 rounded-full outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150"
                  type="button"
                  href="contactUs"
                >
                  Contact Us
                </a>
              </li>
            </ul>
          </div>
        </div>
      </nav>
    </>
  );
}
SvegileNavbar.defaultProps = {
  leftLinks: [],
  rightLinks: [],
  type: "",
};
SvegileNavbar.propTypes = {
  logoImage: PropTypes.string,
  logoText: PropTypes.string,
  // properties to pass to the link object wrapping
  // the logoText and logoImage
  // if you pass a prop named to, the link will be
  // generated using Link from react-router-dom
  logoLink: PropTypes.object,
  color: PropTypes.oneOf([
    "dark",
    "light",
    "transparent",
    "white",
    "black",
    "blueGray",
    "red",
    "orange",
    "amber",
    "emerald",
    "teal",
    "lightBlue",
    "indigo",
    "purple",
    "pink",
  ]),
  type: PropTypes.oneOf(["absolute", "fixed", ""]),
  // array of properties (for both, left and right Links)
  // to pass to the link object
  // if you pass a prop named to, the link will be
  // generated using Link from react-router-dom
  // NOTE: you can generate a dropdown if, inside the
  // // // object you pass dropdown: true as a prop
  // // // please check the DropdownNavbar to see
  // // // what the object should be in this case
  leftLinks: PropTypes.arrayOf(PropTypes.object),
  rightLinks: PropTypes.arrayOf(PropTypes.object),
};
