import React from "react";
import PropTypes from "prop-types";

// components
import HeadingBadge from "../Components/Heading/HeadingBadge";
import CardServiceWithList from "../Components/Cards/CardServiceWithList.js";

import infoAreas from "../Texts/serviceDetails.js";

export default function ServiceDetails() {
  return (
    <>
      <div className="py-12 blog-3">
        <div className="container mx-auto px-4">
          <div className="mb-12">
            <HeadingBadge {...infoAreas.headingBadge} />
          </div>
          <div>
            {infoAreas.infoAreas.map((prop, key) => {
              return <CardServiceWithList {...prop} key={key} />;
            })}
          </div>
        </div>
      </div>
    </>
  );
}
ServiceDetails.defaultProps = {
  cards: [],
  heading3badge: null,
};
ServiceDetails.propTypes = {
  // prop to pass to the Heading3Badge component
  heading3badge: PropTypes.object,
  // it is an array of objects which
  // are represented by the props
  // that can be passed to the CardBlogImageHorizontal,
  // so please check that one out
  cards: PropTypes.arrayOf(PropTypes.object),
};
